import Vue from 'vue'
import Router from 'vue-router'

// Containers
import Full from '@/containers/Full'

// Views
import Dashboard from '@/views/Dashboard'
import ShootdownFlight from '@/views/ShootdownFlight'
import ShootdownScore from '@/views/ShootdownScore'

// Components
import Page404 from '@/components/Page404'

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      redirect: '/displays',
      name: 'Root',
      meta: {
        title: 'Home',
        auth: true
      }
    },
    {
      path: '*',
      redirect: '/404'
    },
    {
      path: '/404',
      component: Page404
    },
    {
      path: '/',
      name: 'Home',
      component: Full,
      meta: {
        title: 'Account'
      },
      children: [
        {
          path: 'code/:id',
          name: 'Displays',
          component: Dashboard,
          meta: {
            title: 'Displays'
          }
        },
        {
          path: 'store/:apiKey/shootdown/:shootdownID/flight/:shootdownFlight',
          name: 'ShootdownFlightView',
          component: ShootdownFlight,
          meta: {
            title: 'Shootdown Flight'
          }
        },
        {
          path: 'store/:apiKey/shootdown/:shootdownID/score/:shootdownPlayerID',
          name: 'ShootdownScoreView',
          component: ShootdownScore,
          meta: {
            title: 'Shootdown Score'
          }
        }
      ]
    }
  ]
})

export default router



