<template>
  <div class="app m-0 p-0">
    <div class="app-body m-0 p-0">
      <main class="main m-0 p-0">
        <div class="container-fluid p-0 m-0">
          <Alerts/>
          <router-view></router-view>
        </div>
      </main>
    </div>
  </div>
</template>

<script>
import { Header as AppHeader, Footer as AppFooter, Alerts } from '../components/'

export default {
  name: 'full',
  components: {
    AppHeader,
    AppFooter,
    Alerts
  },
  data () {
    return {
    }
  },
  computed: {
    name () {
      return this.$route.name
    },
    list () {
      return this.$route.matched
    }
  }
}
</script>



