export default {
  state: {
    accountMessage: false,
    accountMessageText: null,
    accountMessageStyle: null,
    accountMessageIcon: null,
    userMessages: []
  },

  getters: {
    getNotifications: state => state.userMessages,
    countNotifications: state => state.userMessages.length,
    hasAccountMessage: state => state.accountMessage,
    getAccountMessageText: state => state.accountMessageText,
    getAccountMessageStyle: state => state.accountMessageStyle,
    getAccountMessageIcon: state => state.accountMessageIcon
  },

  mutations: {
    resetNotifications: (state) => {
      state.userMessages = []
    },
    addNotification: (state, notificationOptions) => {
      var currentMessages = state.userMessages
      var icon = mapIcons(notificationOptions.icon)
      var styling = mapStyling(notificationOptions.style)
      var message = notificationOptions.message
      var id = Math.floor(Math.random() * 10000)
      if (currentMessages.length > 4) {
        currentMessages.pop()
      }
      state.userMessages = currentMessages.concat([{'id': id, 'message': message, 'styling': styling, 'icon': icon}])
      state.accountMessage = true
      state.accountMessageText = message
      state.accountMessageStyle = notificationOptions.style
      state.accountMessageIcon = icon
    },
    resetAlerts: (state) => {
      state.accountMessage = ''
      state.hasAccountMessage = false
    }
  }
}

function mapIcons (icon) {
  // map icons to display in messages
  var options = {
    'user': 'icon-user-follow',
    'error': 'icon-fire',
    'check': 'icon-check'
  }
  return options[icon]
}

function mapStyling (style) {
  // map styling to display in messages
  var options = {
    'success': 'text-success',
    'danger': 'text-danger'
  }
  return options[style]
}



