<template>
  <div class="col-12 p-0 m-0">

    <b-card class="bg-white" bg-variant="dark" text-variant="dark" v-if="flight.summary.round > flight.summary.total_rounds">
      <div slot="header" class="text-light">
        <strong>Flight:</strong> {{ shootdownFlight }} Winners! <img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
      </div>
      <b-table striped hover :items="winners" :fields="winner_fields">

      </b-table>

    </b-card>

    <b-card class="bg-white" bg-variant="dark" text-variant="dark">
      <div slot="header" class="text-light">
        <strong>Flight:</strong> {{ shootdownFlight }} <span v-if="flight.summary.round <= flight.summary.total_rounds"> - ROUND {{ flight.summary.round }} of {{ flight.summary.total_rounds }} </span><img class="ml-3" src="@/assets/loading.gif" v-if="loading === true">
      </div>
      <b-table striped hover :items="flight.players" :fields="shootdown_fields">

        <template slot="name" slot-scope="data">
          {{ data.item.first_name }} {{ data.item.last_name }}
        </template>

        <template slot="defeated" slot-scope="data">
          <b-badge :variant="getBadge(data.item.defeated)">{{data.value}}</b-badge> <span v-if="data.item.round_defeated > 0"> - R{{ data.item.round_defeated }}</span>
        </template>

      </b-table>

    </b-card>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'

  export default {
    name: 'ShootdownFlight',
    data () {
      return {
        loading: true,
        flight: {},
        winners: [],
        shootdown_fields: [
          {
            key: 'position',
            label: 'P'
          },
          {
            key: 'name',
            label: 'Name'
          },
          {
            key: 'score',
            label: 'Score'
          },
          {
            key: 'xscore',
            label: 'X'
          },
          {
            key: 'defeated',
            label: 'Defeated',
            formatter: (value) => formats.format_yes_no(value)
          }
        ],
        winner_fields: [
          {
            key: 'position',
            label: 'Placed',
            sortable: true,
            sortDirection: 'asc'
          },
          {
            key: 'name',
            label: 'Name'
          }
        ]
      }
    },
    mounted: function () {
      this.apiKey = this.$route.params.apiKey
      this.shootdownID = this.$route.params.shootdownID
      this.shootdownFlight = this.$route.params.shootdownFlight
      this.getShootdownFlight()
    },
    methods: {
      getShootdownFlight () {
        rangepro.get_pub_secure('public/shootdown/' + this.shootdownID + '/flight/' + this.shootdownFlight, this.apiKey, this.handleFlightResponse, 'GetShootdownFlight')
      },
      handleFlightResponse (data, success) {
        if (success) {
          this.flight = data
          this.winners = []
          this.addwinner('1st', this.flight.summary.first.first_name + ' ' + this.flight.summary.first.last_name)
          this.addwinner('2nd', this.flight.summary.second.first_name + ' ' + this.flight.summary.second.last_name)
          this.addwinner('3rd', this.flight.summary.third.first_name + ' ' + this.flight.summary.third.last_name)
        }
        this.loading = false
      },
      getRowCount (items) {
        return items.length
      },
      getBadge (status) {
        return formats.getBadge(status)
      },
      addwinner (placed, name) {
        this.winners.push({
          position: placed,
          name: name
        })
      }
    }
  }
</script>

<style scoped>

</style>



