<template>
  <div class="col-12 p-0 m-0">

    <div ref="card" class="text-dark">

      <b-card v-if="scored" class="bg-white text-center" bg-variant="dark" text-variant="dark">
        <div slot="header" class="text-light">
          <strong>Score Accepted</strong>
        </div>

        <h2 class="text-center">Please take both score cards to the front desk for validation.</h2>

      </b-card>

      <b-card v-if="!scored" class="bg-white" bg-variant="dark" text-variant="dark">
        <div slot="header" class="text-light">
          <strong>Enter Shootdown Score</strong>
        </div>

        <b-form-group
          label="Total Score"
          label-for="playerScore"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="playerScore" type="text" v-model="score.score"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.score.score.required">Score Required</span>
          <span class="form-group__message text-danger" v-if="!$v.score.score.numeric">Score Must be a Number</span>
        </b-form-group>

        <b-form-group
          label="Total X's"
          label-for="playerXCount"
          :label-cols="2"
          :horizontal="true">
          <b-form-input id="playerXCount" type="text" v-model="score.xscore"></b-form-input>
          <span class="form-group__message text-danger" v-if="!$v.score.xscore.required">X's Required</span>
          <span class="form-group__message text-danger" v-if="!$v.score.xscore.numeric">X's Must be a Number</span>
        </b-form-group>

        <b-btn id="btn456873453457876338" class="mt-3" variant="outline-success" block @click="scorePlayer()" :disabled="$v.score.$invalid">Submit Score</b-btn>

      </b-card>

    </div>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import { required, numeric } from 'vuelidate/lib/validators'

  export default {
    name: 'ShootdownScore',
    data () {
      return {
        scored: false,
        score: {
          score: 0,
          xscore: 0
        }
      }
    },
    mounted: function () {
      this.apiKey = this.$route.params.apiKey
      this.shootdownID = this.$route.params.shootdownID
      this.shootdownPlayerID = this.$route.params.shootdownPlayerID
    },
    methods: {
      scorePlayer () {
        var scoring = {
          score: this.score
        }
        rangepro.put_pub_secure('public/shootdown/' + this.shootdownID + '/score/' + this.shootdownPlayerID, this.apiKey, scoring, this.handleScoreResponse, 'PutScoredPlayer')
      },
      handleScoreResponse (data, success) {
        if (success) {
          this.scored = true
        }
      }
    },
    validations: {
      score: {
        score: {
          required,
          numeric
        },
        xscore: {
          required,
          numeric
        }
      }
    }
  }
</script>

<style scoped>

</style>



